@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT sans";

  font-display: swap;
  src: url("../fonts/ptsans.woff2") format(woff2),
    url("../fonts/ptsans.woff") format(woff);
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT sans";

  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format(woff2),
    url("../fonts/ptsansbold.woff") format(woff);
}
